<template>
	<div class="container">
		<my-header title="提交确认"></my-header>
		<section>
			<div>
				<p @click="showAlert(claimApply.antifraudTips,'isReadAnti')">反欺诈提示</p>
				<p @click="showAlert(claimApply.authorizatStatement,'isReadAuth')">申请人授权及申明</p>
				<p>服务人员信息</p>
				<ul>
					<li>
						<span>姓名</span>
						<cube-input @blur="RESETINPUT" v-model="agentName" placeholder="请输入姓名"></cube-input>
					</li>
					<li>
						<span>代理人工号</span>
						<cube-input @blur="RESETINPUT" v-model="agentCode" placeholder="请输入代理人工号"></cube-input>
					</li>
					<li>
						<span>联系方式</span>
						<cube-input @blur="RESETINPUT" v-model="agentMP" type="number" placeholder="请输入联系方式"></cube-input>
					</li>
				</ul>
				<div class="autograph">
					<div>
						<span v-show="spanShow" @click="showPopup()">点击签名</span>
						<img id="xss_20" v-show="imgShow" :src="imgSrc"  @click="showPopup()" title="" alt="" />
					</div>
					<p>签署日期：<span>{{date}}</span></p>
				</div>
				<div class="tip">
					<cube-popup :zIndex=1 type="my-popup" ref="myPopup" position="bottom">
						<sig-nature-encrypt @echoDis="echoDis" @cancel="cancel" ref="sig"></sig-nature-encrypt>
					</cube-popup>
					<cube-checkbox v-model="isRead">本人已阅读《反欺诈提示》与《申请人授权及声明》</cube-checkbox>
				</div>
				<div>
					<foot-button nextBtn="提交" @next="toSubInformation"></foot-button>
				</div>
			</div>
		</section>
	</div>
</template>
<script>
import Vue from 'vue';
import MyHeader from "@/components/model/MyHeader";
import SigNatureEncrypt from "@/components/pages/claimApply/components/SigNatureEncrypt";
import FootButton from "@/components/pages/claimApply/components/FootButton";
import {createAPI,Style,Checkbox,Dialog,Popup,Input,Toast} from 'cube-ui';
import {mapState,mapMutations} from 'vuex';
import {TELREG,ISINPUTNULL} from '@/utils/publicUtils';
import {INSERTSIGNATURE,QUERYINSERTSIGNATURE} from '@/api'
Vue.use(Dialog);
Vue.use(Toast);
export default {
  name: 'ConfirmSub',
  data(){
  	return {
  		imgShow:false,
  		spanShow:true,
  		isRead:false,
  		date:"****年**月**日",
  		isReadAnti:false,
  		isReadAuth:false,
  		echoStr:"",
  		imgSrc:"",
  		agentName:"",
  		agentCode:"",
  		agentMP:"",
  	}
  },
  async created(){
  	let datas=await QUERYINSERTSIGNATURE(this.$route.query);
  	if(!datas.data.data){
  		datas.data.data={}
  	}else{
  		if(datas.data.data.back2){
  			this.date=this.$Moment(datas.data.data.back2).format("YYYY年MM月DD日")
  		}
  	}
  	this.agentName=datas.data.data.agentName;
  	this.agentCode=datas.data.data.agentCode;
  	this.agentMP=datas.data.data.agentMP;
  	this.echoStr=datas.data.data.encrypt;
  	this.imgSrc=datas.data.data.signature;
  	if(this.imgSrc){
  		this.spanShow=false;
  		this.imgShow=true;
  	}
  },
  computed:{
	...mapState(['claimApply','antifraudTips','authorizatStatement'])	
  },
  methods:{
  	...mapMutations(['RESETINPUT']),
  	showPopup() {
      const component = this.$refs['myPopup']
      component.show();
      this.isVisible=true;
      this.$refs.sig.testPopupDialog(20)
    },
  	showAlert(content,str){
  		this[str]=true;
		this.$createDialog({
			type: 'alert',
			content: content,
		}).show()
	},
  	async toSubInformation(){
  		if(this.isRead){
  			(!this.agentMP||TELREG(this,this.agentMP,"联系方式"))&&ISINPUTNULL(this,this.imgSrc,"签名")&&await INSERTSIGNATURE({...this.$route.query,
  				signature:this.imgSrc,
  				encrypt:this.echoStr,
  				agentName:this.agentName,
  				agentCode:this.agentCode,
  				agentMP:this.agentMP
  				}).then((res)=>{
	  				this.$router.push(this.$route.path+"/subinformation")
	  			})
  		}else{
  			const toast = this.$createToast({
		    	time: 2000,
		    	type:"txt",
		    	txt: "<div style='text-align:center;'>请阅读相关说明</div>"
		    })
		    toast.show();
  		}
  	},
  	echoDis(src,val){
  		this.spanShow=false;
  		this.imgShow=true;
  		this.imgSrc=src;
  		this.cancel();
  		this.echoStr=val;
  		this.date=this.$Moment().format("YYYY年MM月DD日")
  	},
  	cancel(){
		const component = this.$refs['myPopup']
		component.hide();
  	}
  },
  components:{
  	MyHeader,SigNatureEncrypt,FootButton,
  	CubeCheckbox:Checkbox,
  	CubePopup:Popup,
  	CubeInput:Input
  }
}
</script>
<style lang="stylus" scoped>
	.container{
		>>>.cube-popup-content{
			width:100%;
			height:300px;
			background:#fff;
		}
		>>>#dialog{
			border:none
		}
		height:100%;
		display:flex;
		flex-direction: column;
		&>section{
			flex:1;
			height:100%;
			overflow-y:auto;
			&>div{
				&>p{
					height:52px;
					line-height:52px;
					text-align:center;
					background:#fff;
				}
				&>p:nth-child(2){
					margin:10px 0;
				}
				&>ul{
					margin:1px 0 10px;
					&>li{
						>>>.cube-input>input{
							text-align:right;
						}
						height:52px;
						line-height:52px;
						background:#fff;
						display:flex;
						justify-content: space-between;
						padding:0 15px;
						border-bottom:1px dashed #ccc;
					}
					&>li:last-child{
						border-bottom:none;
					}
				}
				&>.autograph{
					background:#fff;
					margin:1px 0 10px;
					padding-top:5px;
					padding-bottom:30px;
					&>div{
					    display: flex;
						align-items: center;
					    justify-content: center;
						height:106px;
						img{
							height:100%;
							width:90%;
						}
						&>span{
							display:inline-block;
							padding:14px 36px;
							background:#fff1f1;
							color:#ffad4e;
						}
					}
					&>p{
						text-align:center;
						color:#999;
					}
				}
				&>.tip{
					background:#fff;
					font-size:14px;
					padding:25px 0;
					>>>.cube-checkbox label{
						padding:0
					}
				}
			}
		}
	}
</style>


