<template>
  <div class="sign" ref="sign">
    <div id="dialog">
        <div id="anysign_title" style="color:#333333;display:none">申请人签名</div>

        <div id="btnContainerOuter">
          <div id="btnContainerInner" style="justify-content: space-between;padding: 0 10px;">
              <span onclick="cancelSign()" id="cancel"><i class="fa fa-reply" aria-hidden="true"></i>取消</span>
              <span onclick="clear_canvas()" id="clear"><i class="fa fa-times" aria-hidden="true"></i>清空</span>
              <span onclick="sign_confirm()" id="save"><i class="fa fa-check" aria-hidden="true"></i>保存</span>
          </div>
        </div>

        <div class="box">
          <div id="single_scrollbar">
            <span id="single_scroll_text"></span>
            <span id="single_scrollbar_up"><i class="fa fa-angle-left" aria-hidden="true"></i><span>左移</span></span>
            <span id="single_scrollbar_down"><span>右移</span><i class="fa fa-angle-right" aria-hidden="true"></i></span>
          </div>

          <div id="container"  onmousedown = "return false;">
            <canvas id="anysignCanvas" width="660px">
            </canvas>
          </div>
        </div>

      </div>
  </div>
</template>
<script>
import {Popup} from 'cube-ui';
import {b64} from '@/utils/base64';
export default {
  name: 'drawSignEnc',
  data () {
    return {
      apiInstance:new AnySignApi()
    }
  },
  mounted(){
    this.testAnySign(112321321);
    this.testSetTemplateData()
  },
  methods: {
    showPopup() {
      const component = this.$refs['myPopup']
      component.show()
      setTimeout(() => {
        component.hide()
      }, 1000)
    },
    //添加签名框
    testAddSignatureObj(objId){
        var context_id = objId;
        var signer = new Signer("李明", "11011111111");
        /**
         * 根据坐标定位签名方式
         * @param left 签名图片最左边坐标值，相对于PDF当页最左下角(0,0)点，向上和向右分别为X轴、Y轴正方向
         * @param top 签名图片顶边坐标值，相对于PDF当页最左下角(0,0)点，向上和向右分别为X轴、Y轴正方向
         * @param right 签名图片最右边坐标值，相对于PDF当页最左下角(0,0)点，向上和向右分别为X轴、Y轴正方向
         * @param bottom 签名图片底边坐标值，相对于PDF当页最左下角(0,0)点，向上和向右分别为X轴、Y轴正方向
         * @param pageNo 签名在PDF中的页码，从1开始
         * @param unit 坐标系单位，目前支持"dp"和"pt"
         * @constructor
         */
        //var signerRule = new SignRule_XYZ(20.0, 750.1, 80.2, 650.3, 1, "pt");
        /**
         *根据关键字定位签名位置
         * @param keyWord 关键字字面值
         * @param xOffset X轴偏移量，适配关键字和规则
         * @param yOffset Y轴偏移量，适配关键字和规则
         * @param pageNo 签名在PDF中的页码，第几页查找关键字，正数为正序，当是负数为倒序
         * @param KWIndex KWIndex 第几个关键字
         * @constructor
         */
        //var signerRule = new SignRule_KeyWordV3("申请人签字",3,0,1,1);

        /**
         * 关键字定位方式，寻找PDF中的关键字，根据关键字位置放置签名图片
         * @param keyword 关键字
         * @param keyWordAlignMethod 签字图片和关键字位置关系：等于0时，签字图片和关键字矩形重心重合
         *                            等于1时，签字图片位于关键字正下方，中心线对齐；等于2时，签字图片位于关键字正右方，中心线对齐；
         *                            等于3时，签字图片左上角和关键字右下角重合，可能额外附加偏移量，详见构造函数的offset参数
         * @param keyWordOffset 当keyWordAlignMethod非零时，额外附加的偏移量，单位pt
         * @param pageNo 寻找关键字的PDF起始页码
         * @param KWIndex KWIndex 第几个关键字
         * @constructor
         */
        var signerRule = new SignRule_KeyWordV2("申请人签字", "3", 100, 1,1);
        var signatureConfig = new SignatureConfig(signer,signerRule);
        signatureConfig.title=''
        //1:时间在上、2：时间在下、3：时间在右
        var timeTag = new TimeTag(1,"yyMMdd hh:mm;ss");
        //signatureConfig.timeTag = timeTag;
        signatureConfig.isTSS = false;//是否开始时间戳服务
        signatureConfig.signatureImgRatio = 3.0;
        var res = this.apiInstance.addSignatureObj(context_id,signatureConfig);
        if(res)
        {
           //alert("addSignatureObj "+context_id+" success");
            return res;
        }
        else
        {
            //alert("addSignatureObj "+context_id+" error");
            return res;
        }
    },
    //配置模板数据
    testSetTemplateData(){
       // var formData = "{\"bjcaxssrequest\":{\"submitinfo\":[{\"username\":\"测星雨\",\"identitycardnbr\":\"320902198901191012\"},{\"username\":\"测星雨123\",\"identitycardnbr\": \"320902198901191012\"}]}}";

        var formData = "1";
        var businessId = "123123";//集成信手书业务的唯一标识
        var template_serial = "4000";//用于生成PDF的模板ID

        var res;

        //配置JSON格式签名原文
        /**
         * 设置表单数据，每次业务都需要set一次
         * @param template_type 签名所用的模板类型
         * @param contentUtf8Str 表单数据，类型为Utf8字符串
         * @param businessId 业务工单号
         * @param template_serial 模板序列号
         * @returns {*} 是否设置成功
         */
        res = this.apiInstance.setTemplate(TemplateType.PDF,formData,businessId,template_serial);

        if(res)
        {
         //alert("setTemplateData success");
            return res;
        }
        else
        {
            //alert("setTemplateData error");
            return res;
        }
    },
    testAnySign(channel){
        var res;
        var callback = (context_id,context_type,val)=>
        {
          if(context_type == CALLBACK_TYPE_START_RECORDING || context_type == CALLBACK_TYPE_STOP_RECORDING)
          {
            return;
          }
        
            if(context_type == CALLBACK_TYPE_SIGNATURE)
            {
                //签名回显
                this.$emit("echoDis","data:image/png;base64," + val,b64(this.apiInstance.getUploadDataGram()))
            }
            else if(context_type == CALLBACK_TYPE_DIALOG_CANCEL){
              this.$emit('cancel')
            }
            else if(context_type == EC_COMMENT_ALREADY_SHOW)
            {
                document.getElementById("preview").src = "data:image/png;base64," + val;
            }else if(context_type == CALLBACK_TYPE_ON_MEDIA_DATA)
            {
              var audio = document.createElement("audio");
                if (audio != null && audio.canPlayType && audio.canPlayType("audio/mpeg"))
                {
                    audio.src = "data:image/gif;base64," + val;
                    audio.play();
                }
            }
            //console.log("收到浏览器回调：" + "context_id：" + context_id + " context_type：" + context_type + " value：" + val);
        };//测试回调，将回调数据显示

        ////////////////////////////////////////////////
        //var channel = "10010";//渠道号，由信手书提供，请咨询项目经理
        //初始化签名接口
        res = this.apiInstance.initAnySignApi(callback,channel);

        if(!res){
          //console.log("init error");
        }else{

        }
        ////////////////////////////////////////////////

        //注册单字签字对象20
        res = this.testAddSignatureObj(20);
        if(!res){
            //console.log("testAddSignatureObj error");
        }else{

        }
        ////////////////////////////////////////////////
        //注册一个单位签章

        //var signer = new Signer("小明","110xxxxxx");
        /**
         * 使用服务器规则配置签名
         * @param tid 服务器端生成的配置规则
         * @constructor
         */
       // var signerRule = new SignRule_Tid("888888");
        //var cachet_config = new CachetConfig(signer, signerRule, false);

        //res = this.apiInstance.addCachetObj(cachet_config);
        ////////////////////////////////////////////////

        if(!res){
           console.log("addCachetObj error");
        }else{

        }
        ////////////////////////////////////////////////

        //将配置提交
        res = this.apiInstance.commitConfig();

        if(res){
          //console.log("Init ALL 初始化成功");
        }else{
          //console.log("Init ALL 初始化失败");
        }

        ////////////////////////////////////////////////
    
    },
    //弹出签名框签名
    testPopupDialog(context_id){
      switch (this.apiInstance.showSignatureDialog(context_id))
        {
            case RESULT_OK:
                break;
            case EC_API_NOT_INITED:
               //console.log("信手书接口没有初始化");
                break;
            case EC_WRONG_CONTEXT_ID:
                //console.log("没有配置相应context_id的签字对象");
                break;
        }
    }
  }
}
</script>
<style lang="stylus" scoped>
#dialog{
  width:100%;
}
.box{
  padding:0 5%;
  overflow:hidden;
  height: 100%;
}
#anysign_title{
  display:block;
  //height:34px;
  line-height:34px;
  font-size:24px;
}
#single_scrollbar{
  font-size:18px;
  height:40px;
  display:flex;
  justify-content: center;
}
.fa-angle-left,.fa-angle-right{
  font-size:28px;
  color:#f60;
  margin:0 10px
}
#single_scrollbar_down,#single_scrollbar_up{
  display:flex;
  align-items: center;
}
#single_scrollbar_up{
  margin-right:30px;
}
#container{
  background:rgba(255, 153, 0, 0.0666666666666667);
  border:none;
  height:60%
}
#btnContainerOuter{
  display:block;
  padding:0 5%;
  border-bottom: 1px solid #ccc;
  height:50px;
  &>div{
    height:100%;
    display:flex;
    align-items: center;
    justify-content: space-around;
    font-size:18px;
    .fa{
      color:#ff6600;
      margin-right:8px;
    }
  }
}
</style>


